<style scoped>
    .item {
        display: inline-block;
        width: 100px;
        height: 50px;
        margin: 5px;
        text-align: center;
        line-height: 50px;
        color: white;
        position: relative;
    }

    .defeatHint {
        display: inline-block;
        width: 20px;
        height: 20px;
        background: #2db7f5;
        border-radius: 5px;
        margin: 10px;

    }

    .selectedHint {
        display: inline-block;
        width: 20px;
        height: 20px;
        background: #808695;
        border-radius: 5px;
        margin: 10px;

    }

    .disableHint {
        display: inline-block;
        width: 20px;
        height: 20px;
        background: #c5c8ce;
        border-radius: 5px;
        margin: 10px;

    }

    .imgHint {
        margin: 10px;
    }

    .defeat {
        background: #2db7f5;
        border-radius: 5px;
    }

    .selected {
        background: #808695;
        cursor: not-allowed;
        border-radius: 5px;
    }

    .disable {
        border-radius: 5px;
        background: #c5c8ce;
        cursor: not-allowed;

    }

    .img {
        position: absolute;
        right: 0;
        top: 0;
        z-index: 0;
    }

</style>
<template>
    <div style="padding: 20px">
        <Breadcrumb>
            <BreadcrumbItem to="/team/reservation">所有场馆</BreadcrumbItem>
            <BreadcrumbItem>预约场馆</BreadcrumbItem>
        </Breadcrumb>
        <Divider></Divider>
        <h2>{{detail.name}}</h2>
        <div style="line-height: 50px">
            容纳人数:<span style="display: inline-block;margin-left: 10px">{{detail.accommodate_num}}</span>
        </div>
        <div style="line-height: 50px">
            选择日期:
            <DatePicker :options="options" @on-change="loadData" v-model="time"
                        format="yyyy年MM月dd日" type="date"
                        placeholder="Select date"
                        style="width: 200px" :clearable="false"></DatePicker>
        </div>

        <div>
            <div style="display: flex;line-height: 40px">
                选择时间:
                <span class="defeatHint"> </span>
                <span style="color: #888888">可选</span>
                <span class="disableHint"> </span>
                <span style="color: #888888">禁用</span>
                <img class="imgHint" src="@/assets/select.png" width="20px" height="20px">
                <span style="color: #888888">选中</span>
                <span class="selectedHint"> </span>
                <span style="color: #888888">已被预约</span>


            </div>
            <div>
                <div :class="item.disable?'item disable':item.selected?'item selected ':'item defeat' "
                     v-for="(item ,index) in times"
                     v-bind:key="index" @click="select(item)">
                    {{item.label}}
                    <div v-show="item.chose" class="bgimg">
                        <img class="img" src="@/assets/select.png" width="35px">
                    </div>
                </div>

            </div>
            <Spin size="large" fix v-if="load"></Spin>
        </div>
        <div style="padding: 10px">
            <Button type="success" size="large" style="width: 200px" @click="submit">
                提交
            </Button>
        </div>
    </div>
</template>

<script>
  import api from './api';
  import reservationApi from './reservationApi';
  import time from '@/js/utils/times';

  export default {
    data() {
      return {
        detail: {},
        time: null,
        load: false,
        times: [],
        options: {
          disabledDate(date) {
            return date && date.valueOf() < Date.now() - 86400000;
          }
        },
      };
    },
    mounted() {
      this.time = new Date();
      this.loadData();
      this.times = time.getTime();
    },
    methods: {
      loadData() {
        this.load = true;
        this.times = time.getTime();
        api.detail({
          id: this.$route.params.id,
          date_time: this.$formUtil.dataToStr(this.time)
        }, res => {
          this.detail = res.data.data;
          this.load = false;
          this.detail.disable_time.forEach(value => {
              this.times[parseInt(value) - 1].disable = true;
            }
          );
          this.detail.is_res_time.forEach(value => {
              this.times[parseInt(value) - 1].selected = true;
            }
          );
        }, () => {
          this.load = false;
        });
      },
      select(item) {
        if (item.disable || item.selected) return;
        if (item.chose == null) {
          this.$set(item, 'chose', true);
        } else {
          if (item.chose) {
            this.$set(item, 'chose', false);
          } else {
            this.$set(item, 'chose', true);
          }
        }
      },
      submit() {
        let ids = [];
        this.times.forEach(value => {
          if (value.chose) {
            ids.push(value.id);
          }
        });
        if (ids.length < 1) {
          this.$Message.error('请选择时间段');
          return;
        }
        reservationApi.save({
          venue_id: this.detail.id,
          date: this.$formUtil.dataToStr(this.time),
          enter_team_id: this.$store.state.user.enter_team_id,
          reservation_time: ids,
        }, res => {
          this.loadData();
          this.$Message.success('预约成功');
          this.$router.push({ name: 'reservationMy' });
        }, error => {
          if (error.data && error.data.code && error.data.code === 402) {
            this.loadData();
          }
        });

      }
    }
  };
</script>
